




























































































































.container {
  padding-bottom: 50px;
}
.image-box {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
  }
  .mask {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 120px;
    background-color: #222;
    opacity: 0.55;
    filter: blur(60px);
  }
  .info-box {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    box-sizing: border-box;
    padding: 20px 30px;
    color: #fff;
    z-index: 8;
    .cinema-name {
      font-size: 36px;
      line-height: 1em;
      margin-bottom: 10px;
    }
    .snack-title {
      line-height: 1em;
      font-size: 30px;
    }
  }
}
.price-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
  padding-left: 0;
  margin-left: 30px;
  border-bottom: 1px solid #f0f0f0;
  .price-num {
    color: #f03d37;
    font-size: 50px;
    .yuan {
      font-size: 24px;
    }
  }
  .buy-btn {
    padding: 20px 30px;
    color: #fff;
    font-size: 36px;
    background-color: #f90;
    border-radius: 8px;
  }
  .cinema-price {
    margin-left: 15px;
    font-size: 28px;
    color: #999;
  }
  &:last-child {
    border: none;
  }
}

.deal-tags-list {
  font-size: 28px;
  color: #6bbd00;
  .item {
    display: inline-block;
    margin-right: 30px;
  }
}

.span-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  background-size: 100% 100%;
  margin-right: 15px;
  vertical-align: middle;
  background-image: url(data:image/png;base64,);
}

.sold {
  color: #666;
}

.icon-04 {
  color: #666;
  margin-right: 15px;
}

.section {
  border-bottom: 20px solid #f5f5f5;
  font-size: 28px;
}

.section-title {
  padding: 30px 30px 30px 0;
  margin-left: 30px;
  color: #999;
  font-size: 34px;
  border-bottom: 1px solid #f0f0f0;
}

.set-title {
  position: relative;
  top: -1px;
  background: #f9f9f9;
  text-align: center;
  padding: 20px 0;
  color: #999;
  line-height: 1em;
  font-size: 26px;
}

.detail-group {
  display: flex;
  margin-left: 30px;
  color: #666;
  font-size: 28px;
  border-bottom: 1px dashed #ddd8ce;
  &:last-child {
    border: none;
  }
  .center {
    width: 15%;
    padding: 30px 0;
    border-right: 1px dashed #ddd8ce;
    border-left: 1px dashed #ddd8ce;
    text-align: center;
  }

  .right {
    width: 70%;
    padding: 30px 0;
    padding-left: 0;
  }
  .left {
    width: 15%;
    padding: 30px 0;
    text-align: center;
  }
}

.terms-item {
  margin: 30px;
}

.tip-title {
  color: #666;
  margin-bottom: 10px;
  font-size: 30px;
}

.tip-des {
  color: #999;
  font-size: 28px;
}
